import axios from 'axios'
import Router from '../router'

export const HTTP = axios.create({
  baseURL: 'https://api.easyvedaz.com/api'
  // baseURL: 'http://localhost:51689/api'
})

HTTP.interceptors.request.use(function (config) {
  var user = JSON.parse(localStorage.getItem('user'))
  const token = user ? user.accessToken : null
  config.headers.authorization = token ? 'Bearer ' + token : ''
  console.log(config.headers.authorization)
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})

// Add a response interceptor
let isRefreshing = false
HTTP.interceptors.response.use(function (response) {
  return response
}, function (error) {
  // Do something with response error
  var orignalRequest = error.config
  console.log('orignalrequest', orignalRequest)
  console.log('isrefreshing', isRefreshing)
  if (error.config && error.response && error.response.status === 401 && !isRefreshing) {
    isRefreshing = true
    var rt = JSON.parse(localStorage.getItem('user')).refreshToken
    var reqObj = {
      refreshToken: rt,
      GrantType: 'refresh_token'
    }
    HTTP.post('token', reqObj).then(response => {
      localStorage.setItem('user', JSON.stringify(response.data))
      isRefreshing = false
    }).then(r => {
      const token = JSON.parse(localStorage.getItem('user')).accessToken
      orignalRequest.headers['Authorization'] = 'Bearer ' + token
      return HTTP(orignalRequest)
    }).catch(e => {
      console.error('retry failed')
      Router.push({
        path: 'Login'
      })
    })
  }
  return Promise.reject(error)
})



